import props from '@/common/mixin/props.js'
export default {
	mixins: [props],
	props: {
		height: {
			type: String,
			default: '350'
		},
		preview: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {};
	},
	computed: {
		getStyle() {
			return `height:${this.height}rpx`
		},
		getUrls() {
			let arr = []
			for (let key in this.resdata) {
				arr.push(this.resdata[key].src)
			}
			console.log(arr)
			return arr

		}
	},
	methods: {
		event(item, index) {
			if (this.preview) {
				uni.previewImage({
					urls: this.getUrls,
					current: index
				})
			} else {
				console.log('点击了轮播图')
			}
		}
	}
}
